/* ------------------- */
/* RECOMMENDED          */
/* ------------------- */

#recommended {
  background-color: #fff;
  padding: 140px 0 200px;
  /* border: 1px solid red; */
}
/*-------------------
Recommended Top
---------------------*/

.rc_top {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.rc_top p {
  text-align: justify;
  line-height: 40px;
  padding-top: 40px;
  padding-right: 70px;
}

/*-------------------
Recommended Bottom
---------------------*/

.rc_bottom {
  display: flex;
  position: relative;
  margin-top: 67px;
}

.rc_bottom img {
  width: 40%;
  z-index: 1;
}

.rc_list {
  background-color: #E2D0CA;
  padding: 79px 93px 77px 0;
  width: 65.8%;
  display: flex;
  justify-content: end;
  align-items: center;
  position: absolute;
  top: 79px;
  right: 0;
}

.rc_list ul {
  letter-spacing: 1.2px;
}

.rc_list li {
  position: relative;
  margin-top: 18px;
  padding-left: 45px;
}

.rc_list li:first-child {
  margin-top: 0;
}

.rc_list li::after {
  content: "";
  display: block;
  position: absolute;
  top: 10px;
  left: 0;
  width: 12px;
  height: 8px;
  border-left: 1px solid #c38c80;
  border-bottom: 1px solid #c38c80;
  transform: rotate(-45deg);
}


@media (max-width:1090px){
  .rc_list {
    padding: 60px 40px 60px 0;
  }
}
@media (max-width:980px){
  .rc_list {
    padding: 50px 30px 50px 70px;
  }
  .rc_list li{
    padding-left: 25px;
    font-size: 16px;
  }
}


/*-----------------
      Mobile
-------------------*/

/*------------------------
  Recommended Top - Mobile
--------------------------*/


@media (max-width:768px){
  .rc_top {
    display: block;
    grid-template-columns: 1fr 90% 1fr;
  }
  .rec-top_wrapper{
    grid-column: 2;
  }
  .rc_top p {
  padding: 0;
  line-height: 30px;
  }
  #recommended {
    background-color: #fff;
    padding: 80px 0 450px;
  }
}

/*---------------------------
  Recommended Bottom - Mobile
-----------------------------*/
@media (max-width:768px){

  #recommended{
    display: block;
  }
  .rc_bottom{
    display: grid;
    grid-template-columns: 1fr 90% 1fr;
    margin-top: 60px;
  }

  .rc_bottom img {
    width: 95%;
    z-index: 2;
    grid-column: 1/3;
  }

  .rc_list {
    width: 94.87vw;
    left: 0;
    padding: 0;
    padding-top:6.25em ;
    padding: 6em 45px 57px 41px;
    padding-left: 0; 
    display: grid;
    place-content: center;
    z-index: 1;
    top: 70%;
    grid-column: 2/3;
  }

  .rc_fs{
    padding-left: 30px;
    padding-top: 50px;
  }

  .rc_fs li{
    letter-spacing: 1px;
  }

  .rc_list li {
    position: relative;
    margin-top: 20px;
    padding-left: 30px;
  }
  
  .rc_list li:first-child {
    margin-top: 0;
  }
}

@media(max-width:600px){
  .rc_fs li{
    font-size: 16px;
    letter-spacing: 0;
  }
  .rc_fs{
    padding-left: 30px;
    padding-top: 25px;
  }

}

@media(min-width:600px) and (max-width:768px){
  .rc_fs{
    padding-left: 30px;
    padding-top: 100px;
  }
}
